:root{
    --background: #1f242d;
    --second:#323946;
    --text-color:#fff;
    --main-color: #0ef;
}

body{
    margin: 0;
    padding: 0;
}

.containerNavbar{
    background-color: var(--background);
    box-shadow: 1px 4px 10px var(--second);
    top: 0;
    left: 0;
    z-index: 100;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    padding: 1.5rem 9%;
}
.containerNavbar .navbar{
    list-style: none;
    margin-right: .6rem;
}

.containerNavbar .navbar ul{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

.containerNavbar .navbar ul li{
    list-style: none;
    /* margin-right: 100px; */
}

.containerNavbar .navbar ul li a{
    text-decoration: none;
    color: var(--text-color);
    font-size: 1.7rem;
    margin-left: 4rem;
    transition: .3s;
}

.containerNavbar .navbar ul li a:hover{
    color: #0ef;
}

.logo a{
    font-size: 2rem;
    color: white;
    font-weight: 600;
    cursor: default;
}

span{
    color: #0ef;
}