
:root{
    --background: #1f242d;
    --second:#323946;
    --text-color:#fff;
    --main-color: #0ef;
}
.containerMain{
    background: var(--background);
    color: var(--text-color);
    min-height: 100vh;
    padding: 10rem 9% 2rem;
}
.main{
    display: flex;
    justify-content: center;
    align-items: center;
}
.main h3{
    font-size: 3.2rem;
    font-weight: 700;
}

.main h3:nth-of-type(2){
    margin-bottom: 1.8rem;

}

span{
    color: var(--main-color);
}

.main h1{
    font-size: 5.5rem;
    font-weight: 700;
    line-height: 1.3;
}
.IMG{
    width: 35vw;
}
.main p{
    font-size: 1.4rem;
}
.icon a{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 4rem;
    height: 4rem;
    background: transparent;
    border: .2rem solid var(--main-color);
    border-radius: 50%;
    font-size: 2rem;
    color: var(--main-color);
    margin: 3rem 1.5rem 3rem 0;
    transition: .5s ease;
}

.icon a:hover{
    background: var(--main-color);
    color:var(--second) ;
    box-shadow: 0 0 1rem var(--main-color);
}

.but{
    display: inline-block;
    padding: 1rem 2.8rem;
    background: var(--main-color);
    border-radius: 4rem;
    box-shadow: 0 0 1rem var(--main-color);
    font-size: 1.6rem;
    color: var(--second);
    letter-spacing: .1rem;
    font-weight: 600;
    transition: .5s ease;
}

.but:hover{
    box-shadow: none;
}

.containerAbout{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    background: var(--second);
    padding: 10rem 9% 2rem;
}

.img-about{
    width: 35vw;
}
.about .Tentang{
    /* text-align: center; */
    font-size: 4.5rem;
    color: white;
}

h2{
    text-align: left;
    line-height: 1.2;
    color: white;
}

.about h3{
    font-size: 2.6rem;
    color: white;
}

.about p{
    font-size: 1.6rem;
    margin:2rem 0 3rem;
    color: white;
    width: 750px;
    /* font-family: ; */
    /* background-color: red; */
}

.containerPort{
    background-color: var(--second);
    padding: 11rem 9% 2rem;
}

.lat{
    margin-bottom: 4rem;
    color: white;
    text-align: center;
    font-size: 4.5rem;
    padding: 4rem;
}

.port{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    align-items: center;
    gap: 2.5rem;
    margin-left: -8.5rem;
}

.containerPort .port-box{
    position: relative;
    border-radius: 2rem;
    box-shadow: 0 0 1rem var(--second);
    overflow: hidden;
    display: flex;
    width: 30rem;
    height: 30rem;
}

.port-box img{
    width: 100%;
    transition: .5s ease;
}

.port-box .layer{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(rgb(0,0,0,.1),var(--main-color));
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    padding: 0 4rem;
    color: white;
    transform: translateY(100%);
    transition: .5s;
}

.layer h4{
    font-size: 3rem;
}

.layer p{
    font-size: 1.6rem;
    margin: .3rem 0 1rem;
}

.layer a{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 5rem;
    height: 5rem;
    background: var(--text-color);
    border-radius: 50%;
}

.layer a i{
    font-size: 2rem;
    color: var(--second);
}

.port-box:hover img{
    transform: scale(1.1);
}

.port-box:hover .layer{
    transform: translateY(0);
}

.containerContact h2{
    margin-bottom: 3rem;
}

.containerContact form{
    max-width: 70rem;
    margin: 1rem auto;
    text-align: center;
    margin-bottom: 3rem;
}

.containerContact form .input{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.containerContact form .input input,
.containerContact form textarea{
    width: 100%;
    padding: 1.5rem;
    font-size: 1.6rem;
    color: var(--text-color);
    background-color: var(--second);
    border-radius: .8rem;
    margin: .7rem 0;
}

.containerContact form .input input{
    width: 49%;
}

.butt{
    display: inline-block;
    padding: 1rem 2.8rem;
    background: var(--main-color);
    border-radius: 4rem;
    box-shadow: 0 0 1rem var(--main-color);
    font-size: 1.6rem;
    color: var(--second);
    letter-spacing: .1rem;
    font-weight: 600;
    transition: .5s ease;
    text-align: center;
}

.butt:hover{
    box-shadow: none;
    cursor: pointer;
}

.containerContact form textarea{
    resize: none;
}

.containerSkill {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    margin-right: auto;
    margin-left: auto;
}

@media (min-width: 768px){
    .containerSkill{
        margin: 0 auto;
    }
}

@media (min-width: 992px){
    .containerSkill{
        max-width: 960px;
    }
}

@media (min-width: 1200px){
    .containerSkill{
        max-width: 1140px;
    }
}

@media (min-width: 1400px){
    .containerSkill{
        max-width: 1350px;
    }
}

.section{
    padding: 10rem;
    position: relative;
    background-color: var(--background);
}

.section .skill{
    margin-top: 2rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    gap: 20px;
}

.skill{
    -webkit-box-flex: 0;
    -ms-flex: 0 0 48%;
    flex: 0 0 48%;
    max-width: 0 0 48%;
}

.info{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.info p{
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 1.3rem;
    color: white;
    opacity: 1;
    -webkit-animation: showText 0.5s 1s linear forwards;
    animation: showText 0.5s 1s linear forwards;
}

.proges{
    margin-top: .5rem;
    height: 10px;
    width: 100%;
    background-color: white;     
    border-radius: 50px;
    position: relative;
} 

.proges span{
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transform-origin: left;
    transform-origin: left;
    -webkit-animation: animate 1s cubic-bezier(1,0, 0.5,1) forwards;
    animation: animate 1s cubic-bezier(1,0, 0.5,1) forwards;
    height: 100%;
    border-radius: 50px;
    position: absolute;
    background-color: var(--main-color);
    transition: 1s all;
} 

.proges span.animate{
    -webkit-animation: animate 1.5s 0.5s cubic-bezier(1,0,0.5,1) forwards;
    animation: animate 1.5s 0.5s cubic-bezier(1,0,0.5,1) forwards;
} 

@-webkit-keyframes showText{
    100%{
        opacity: 1;
    }
}

@keyframes showText{
    100%{
        opacity: 1;
    }
}

@-webkit-keyframes animate{
    100%{
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
    }
}

@keyframes animate{
    100%{
        -webkit-transform: scaleX(1);
        transform: scaleX(1);
    }
} 
